/**
 * 
 */
export const checkAnalyAuthor = function () {
  let user = Cookies.getJSON('user');
  if (!user) {
    return false
  }
  return user.authority >= 3;
};

/**
 * gmt
 * 
 * 
 * 
 */
export const checkGmtAuthor = function (authorLv) {
  let user = Cookies.getJSON('user');
  if (!user) {
    return false
  }

  if (user.authority === 0) {
    return false
  }

  return user.authority <= authorLv;
};




