<template>
  <DatePicker ref="picker" class="date-picker" size="large" :clearable="false" confirm type="daterange"
              format="yyyyMMdd" :options="datePickerOptions"
              placement="bottom-start" placeholder="" :value="getSelectedDateRange()" @on-change="onChange"
              style="width: 270px">
  </DatePicker>
</template>

<script>
  import * as services from '@/services'
  import moment from 'moment'

  export default {
    data() {
      return {
        choosedDateFrom: null,
        choosedDateTo: null,

        // 
        datePickerOptions: {
          shortcuts: [
            {
              text: '',
              value() {
                const end = new Date();
                const start = (new Date()).setTime(end.getTime() - 3600 * 1000 * 24 * 6);
                return [start, end];
              }
            },
            {
              text: '',
              value() {
                const end = new Date();
                const start = (new Date()).setTime(end.getTime() - 3600 * 1000 * 24 * 13);
                return [start, end];
              }
            },
            {
              text: '',
              value() {
                const end = new Date();
                const start = (new Date()).setTime(end.getTime() - 3600 * 1000 * 24 * 29);
                return [start, end];
              }
            }
          ],

          disabledDate(date) {
            if (date) {
              // 
              if (date.valueOf() > Date.now()) {
                return true;
              }
            }
          }
        }
      }
    },

    methods: {
      onChange(dates) {
        this.choosedDateFrom = moment(dates[0], 'YYYYMMDD').format('YYYY-MM-DD');
        this.choosedDateTo = moment(dates[1], 'YYYYMMDD').format('YYYY-MM-DD');
        services.setSelectedDateRange(this.choosedDateFrom, this.choosedDateTo);
      },

      getSelectedDateRange() {
        return [services.getSelectedDateRangeFrom(), services.getSelectedDateRangeTo()];
      }
    }
  }
</script>



