<template>
  <div>
    <Form ref="form" :model="formModel" :label-width="80">
      <FormItem label="">
        <Input v-model="formModel.mailTitle" placeholder="..."></Input>
      </FormItem>
      <FormItem label="">
        <Input v-model="formModel.mailContent" type="textarea" :autosize="{minRows: 6, maxRows: 8}"
               placeholder="..."></Input>
      </FormItem>
      <FormItem label="">
        <FormItem
          v-for="(item, index) in formModel.mailItemListOfCascaderFormat" :key="index">
          <div style="margin-bottom: 5px;">
            <Cascader v-model="formModel.mailItemListOfCascaderFormat[index].cascaderValue" :data="itemCfgCascaderData"
                      placeholder="" trigger="hover" style="display: inline-block" filterable></Cascader>
            <span style="margin-left: 5px">x</span>
            <Input v-model="formModel.mailItemListOfCascaderFormat[index].itemNum" placeholder=""
                   style="width: 80px; margin-left: 5px;"></Input>
            <Button @click="handleRemoveMailItem(index)"></Button>
          </div>
        </FormItem>
        <Button type="dashed" icon="plus-round" long @click="handleAddMailItem" style="width: 200px">{{
          formModel.mailItemListOfCascaderFormat.length === 0 ? '' : ''}}
        </Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
  import * as services from '@/services'

  export default {
    props: {
      // 
      title: {
        type: String,
        default: ''
      },

      // 
      content: {
        type: String,
        default: ''
      },
    },

    data() {
      return {
        formModel: {
          mailTitle: '',
          mailContent: '',
          mailItemListOfCascaderFormat: [],
        },
        itemCfgCascaderData: [] // 
      };
    },

    created() {
      // 
      this.formModel.mailTitle = this.title;
      this.formModel.mailContent = this.content;

      // 
      this.fetchItemCfgList();
    },

    methods: {
      // 
      getData() {
        // 
        var title = this.formModel.mailTitle.trim();
        if (title.length === 0) {
          this.$Message.error('');
          return null;
        }

        // 
        var content = this.formModel.mailContent.trim();
        if (content.length === 0) {
          this.$Message.error('');
          return null;
        }

        // 
        var itemList = [];
        for (var i = 0; i < this.formModel.mailItemListOfCascaderFormat.length; i++) {
          var formMailItem = this.formModel.mailItemListOfCascaderFormat[i];

          // cascaderValueid
          if (formMailItem.cascaderValue.length === 0) {
            this.$Message.error('');
            return;
          }

          // id
          var itemId = parseInt(
            formMailItem.cascaderValue[
            formMailItem.cascaderValue.length - 1
              ].trim()
          );
          if (isNaN(itemId) || itemId <= 0) {
            this.$Message.error('id');
            return;
          }

          // 
          var itemNum = parseInt(formMailItem.itemNum);
          if (isNaN(itemNum) || itemNum <= 0) {
            this.$Message.error('');
            return;
          }

          // 
          itemList.push({id: itemId, num: itemNum});
        }

        return {
          title: title,
          content: content,
          itemList: itemList
        };
      },

      // 
      fetchItemCfgList() {
        services.getHttpClient().post({
          url: '/xy/gmt/query_item_list'
        }, (error, response, body) => {
          if (error) {
            this.$Message.error(error.toString());
            return;
          }

          if (body.code != 0) {
            this.$Message.error(body.msg);
            return;
          }

          if (!body.data) {
            return
          }

          // Cascader
          // 
          this.itemCfgCascaderData.push({
            value: 'currency',
            label: '',
            children: (() => {
              var result = [];
              body.data.amount_list.forEach(element => {
                result.push({
                  value: element.id.toString(),
                  label: element.name
                });
              });

              return result;
            })()
          });

          // 
          this.itemCfgCascaderData.push({
            value: 'prop',
            label: '',
            children: (() => {
              var result = [];
              body.data.item_list.forEach(element => {
                result.push({
                  value: element.id.toString(),
                  label: element.name
                });
              });

              return result;
            })()
          });

          // 
          this.itemCfgCascaderData.push({
            value: 'hufu',
            label: '',
            children: (() => {
              var result = [];
              body.data.hufu_list.forEach(element => {
                result.push({
                  value: element.id.toString(),
                  label: element.name
                });
              });

              return result;
            })()
          });

          // 
          this.itemCfgCascaderData.push({
            value: 'equip',
            label: '',
            children: (() => {
              var result = [];
              body.data.equip_list.forEach(element => {
                result.push({
                  value: element.id.toString(),
                  label: element.name
                });
              });

              return result;
            })()
          });
        });
      },

      // 
      handleAddMailItem() {
        this.formModel.mailItemListOfCascaderFormat.push({
          cascaderValue: [], // Cascadervalueid
          itemNum: 1
        });
      },

      // 
      handleRemoveMailItem(itemIndex) {
        this.formModel.mailItemListOfCascaderFormat.splice(itemIndex, 1);
      },

      handleReset() {
        this.$refs['form'].resetFields();
      },

      /**
       * 
       * @param zoneIdListStr idid'1,2,3'
       * @param type 1:  2: 
       * @param roleId idtype1roleId
       * @param title 
       * @param content 
       * @param itemList 
       */
      sendMail(zoneIdListStr, type, roleId, title, content, itemList) {
        services.getHttpClient().post({
          url: '/xy/gmt/send_mail',
          body: {
            type: type,
            role_id: type === 1 ? 0 : roleId,
            server_id: zoneIdListStr,
            title: title,
            content: content,
            mail_item: itemList
          }
        }, (error, response, body) => {
          if (error) {
            this.$Message.error(error.toString());
            this.$emit('on-change');
            return;
          }

          if (body.code != 0) {
            this.$Message.error(body.msg);
            this.$emit('on-change');
            return;
          }

          this.$Message.success('');
          this.$emit('on-change');
        });
      }
    }
  };
</script>



