<template>
  <div>
    <RegionSelect ref="regionSelect"></RegionSelect>
    <br>
    <Form :model="formModel" :label-width="80">
      <FormItem label="">
        <RadioGroup v-model="formModel.receiverZonesRange">
          <Radio label="some"></Radio>
          <Radio label="all"></Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="" v-if="formModel.receiverZonesRange === 'some'">
        <ZoneSelect v-model="formModel.selectedZoneIdList" multiple></ZoneSelect>
      </FormItem>
      <FormItem label="">
        <Input v-model="formModel.messageContent" placeholder="..."></Input>
      </FormItem>
      <FormItem label="">
        <RadioGroup v-model="formModel.sortId">
          <Radio label="30"></Radio>
          <Radio label="20"></Radio>
          <Radio label="10"></Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="">
        <RadioGroup v-model="formModel.state">
          <Radio label="2"></Radio>
          <Radio label="1"></Radio>
          <Radio label="3"></Radio>
        </RadioGroup>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit"></Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
  import * as services from '@/services'
  import ZoneSelect from '@/components/ZoneSelect'
  import RegionSelect from '@/components/RegionSelect'

  export default {
    data() {
      return {
        formModel: {
          receiverZonesRange: 'some',
          selectedZoneIdList: [],
          messageContent: '', // 
          state: "2", // 1-,2-,3-
          sortId: "30", // ,
        }
      };
    },

    methods: {
      handleSubmit() {
        if (!services.checkGmtAuthor(1)) {
          this.$Message.error(",!");
          return
        }

        // 
        var messageContent = this.formModel.messageContent.trim();
        if (messageContent.length === 0) {
          this.$Message.error('');
          return
        }

        var state = this.formModel.state;
        var sortId = this.formModel.sortId;

        // id
        var zoneIdList = [];

        // 
        var isToAll = (this.formModel.receiverZonesRange === 'all');

        if (isToAll) {
          // 
          services.getHttpClient().post({
            url: '/xy/gmt/query_server_list',
            body: {
              include_merged: false
            }
          }, (error, response, body) => {
            if (error) {
              this.$Message.error(error.toString());
              return;
            }

            if (body.code != 0) {
              // this.$Message.error(body.msg);
              return;
            }

            if (!body.data || body.data.length === 0) {
              return
            }

            var zoneList = body.data;
            for (var i = 0; i < zoneList.length; i++) {
              var zone = zoneList[i];
              zoneIdList.push(zone.id);
            }

            // elseelse
            this.onGetZoneIdList(zoneIdList, messageContent, state, sortId);
          });
        } else {
          zoneIdList = this.formModel.selectedZoneIdList;
          this.onGetZoneIdList(zoneIdList, messageContent, state, sortId);
        }
      },

      onGetZoneIdList(zoneIdList, messageContent, state, sortId) {
        if (zoneIdList.length === 0) {
          this.$Message.error('');
          return
        }

        // 
        this.sendMarquee(zoneIdList.join(','), messageContent, state, sortId);
      },

      handleReset() {
        console.log('handleReset');
      },

      /**
       * 
       * @param zoneIdListStr idid'1,2,3'
       * @param messageContent 
       * @param state 
       * @param sortId 
       */
      sendMarquee(zoneIdListStr, messageContent, state, sortId) {
        console.log(state + "-" + sortId)
        services.getHttpClient().post({
          url: '/xy/gmt/send_marquee',
          body: {
            server_id: zoneIdListStr,
            content: messageContent,
            state: parseInt(state),
            sort_id: parseInt(sortId)
          }
        }, (error, response, body) => {
          if (error) {
            this.$Message.error(error.toString());
            return;
          }

          if (body.code != 0) {
            this.$Message.error(body.msg);
            return;
          }

          this.$Message.success('');
        });
      }
    },

    components: {
      ZoneSelect,
      RegionSelect
    }
  }
</script>



