<template>
  <div>
    <RegionSelect ref="regionSelect"></RegionSelect>
    <br>
    <Form :model="formModel" :label-width="80">
      <FormItem label="">
        <RadioGroup v-model="formModel.receiverZonesRange">
          <Radio label="some"></Radio>
          <Radio label="all">()</Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="" v-if="formModel.receiverZonesRange === 'some'">
        <ZoneSelect v-model="formModel.selectedZoneIdList" multiple></ZoneSelect>
      </FormItem>
      <FormItem label="">
        <Select v-model="formModel.interval" filterable placeholder="">
          <Option v-for="item in intervalList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </FormItem>
      <FormItem label="">
        <Input v-model="formModel.messageContent" placeholder="..."></Input>
      </FormItem>
      <FormItem label="">
        <RadioGroup v-model="formModel.sortId">
          <Radio label="30"></Radio>
          <Radio label="20"></Radio>
          <Radio label="10"></Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="">
        <RadioGroup v-model="formModel.state">
          <Radio label="2"></Radio>
          <Radio label="1"></Radio>
          <Radio label="3"></Radio>
        </RadioGroup>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit"></Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
  import * as services from '@/services'
  import ZoneSelect from '@/components/ZoneSelect'
  import RegionSelect from '@/components/RegionSelect'

  export default {
    data() {
      return {
        formModel: {
          receiverZonesRange: 'some',
          selectedZoneIdList: [],
          messageContent: '', // 
          interval: '', // ,
          state: "2", // 1-,2-,3-
          sortId: "30", // ,
        },
        intervalList: [
          {value: '5', label: '5'},
          {value: '10', label: '10'},
          {value: '15', label: '15'},
          {value: '30', label: '30'},
          {value: '60', label: '1'},
          {value: '120', label: '2'},
          {value: '180', label: '3'},
          {value: '360', label: '6'},
        ]
      };
    },

    methods: {
      handleSubmit() {
        if (!services.checkGmtAuthor(1)) {
          this.$Message.error(",!");
          return
        }

        // 
        var messageContent = this.formModel.messageContent.trim();
        if (messageContent.length === 0) {
          this.$Message.error('');
          return
        }

        var state = this.formModel.state;
        var sortId = this.formModel.sortId;
        var interval = this.formModel.interval;
        if (interval.length === 0) {
          this.$Message.error('');
          return
        }

        var zoneIdList = [];
        if (this.formModel.receiverZonesRange === 'some') {
          zoneIdList = this.formModel.selectedZoneIdList;
        }

        this.insertMarquee(zoneIdList.join(','), messageContent, state, sortId, interval);
      },

      handleReset() {
        console.log('handleReset');
      },

      /**
       * 
       * @param zoneIdListStr idid'1,2,3'
       * @param messageContent 
       * @param state 
       * @param sortId 
       * @param interval 
       */
      insertMarquee(zoneIdListStr, messageContent, state, sortId, interval) {
        // console.log("zone:" + zoneIdListStr + " interval:" + interval)
        services.getHttpClient().post({
          url: '/xy/gmt/insert_marquee',
          body: {
            server_id: zoneIdListStr,
            content: messageContent,
            state: parseInt(state),
            sort_id: parseInt(sortId),
            interval: parseInt(interval),

          }
        }, (error, response, body) => {
          if (error) {
            this.$Message.error(error.toString());
            return;
          }

          if (body.code != 0) {
            this.$Message.error(body.msg);
            return;
          }

          this.$Message.success('');
        });
      }
    },

    components: {
      ZoneSelect,
      RegionSelect
    }
  }
</script>



