<template>
  <div>
    <RegionSelect ref="regionSelect"></RegionSelect>
    <br>
    <Form :model="formModel" :label-width="80">
      <FormItem label="">
        <ZoneSelect v-model="formModel.selectedZoneId"></ZoneSelect>
      </FormItem>
      <FormItem label="id">
        <Input v-model="formModel.roleId" placeholder="id-id"></Input>
      </FormItem>
      <FormItem label="">
        <RadioGroup v-model="formModel.type">
          <Radio label="ban"></Radio>
          <Radio label="allow"></Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="()" v-if="formModel.type === 'ban'">
        <Input v-model="formModel.duration" placeholder=""></Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit"></Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
  import * as services from '@/services'
  import ZoneSelect from '@/components/ZoneSelect'
  import RegionSelect from '@/components/RegionSelect'

  export default {
    data() {
      return {
        formModel: {
          selectedZoneId: '',
          roleId: '',
          type: 'ban',
          duration: '',
        }
      }
    },

    methods: {
      handleSubmit() {
        // id
        var zoneId = this.formModel.selectedZoneId;
        if (typeof zoneId === 'string') {
          this.$Message.error('');
          return
        }

        // id
        var roleId = parseInt(this.formModel.roleId);
        if (!roleId || roleId < 0) {
          this.$Message.error('id');
          return
        }

        // ()
        var duration = 0;
        if (this.formModel.type === 'ban') {
          duration = parseInt(this.formModel.duration);
          if ((duration !== 0 && !duration) || duration <= 0) {
            this.$Message.error('');
            return
          }
        }

        this.banChat(zoneId, roleId, duration);
      },

      /**
       * 
       * @param zoneId id
       * @param roleId id
       * @param duration 
       */
      banChat(zoneId, roleId, duration) {
        services.getHttpClient().post({
          url: '/xy/gmt/ban',
          body: {
            ban_type: 1,
            role_id: roleId,
            server_id: zoneId,
            ban_duration: duration
          }
        }, (error, response, body) => {
          if (error) {
            this.$Message.error(error.toString());
            return;
          }

          if (body.code != 0) {
            // this.$Message.error(body.msg);
            return;
          }

          this.$Message.success('');
        });
      }
    },

    components: {
      ZoneSelect,
      RegionSelect
    }
  }
</script>



