<template>
  <div>
    <RegionSelect ref="regionSelect"></RegionSelect>
    <br>
    <Form :model="formModel" :label-width="80">
      <!-- <FormItem label="">
         <Select @on-change="onOperateChanged" v-model="formModel.currencyType"> filterable placeholder="">
          <Option v-for="operate in operateList" :key="operate.name" :value="operate.type">{{ operate.name }}</Option>
        </Select>
      </FormItem> -->
      <FormItem label="">
        <RadioGroup v-model="formModel.typeSelect">
         <Radio label="tel"></Radio>
          <Radio label="playerId">ID</Radio>
        </RadioGroup>
      </FormItem>
       <FormItem label="">
              <Input v-model="formModel.roleId" placeholder="ID"></Input>
        </FormItem>
      <FormItem label="">
        <RadioGroup v-model="formModel.type">
          <Radio label="ban"></Radio>
          <Radio label="allow"></Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="()" v-if="formModel.type === 'ban'">
        <Input v-model="formModel.banTime"  placeholder="0"></Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit"></Button>
         <Button type="primary" @click="searchSubmit"></Button>
      </FormItem>
      <Table border :columns="tableColumns" :data="tableRows"></Table>
    </Form>
  </div>
</template>

<script>
  import * as services from '@/services'
  import RegionSelect from '@/components/RegionSelect'

  export default {
    data() {
      return {
        formModel: {
          selectedZoneId: '',
          roleId: '',
          type: 'ban',
          duration: '',
          currencyType:1,
          operate:1,
          banTime:"",
          typeSelect: 'tel',
        },

        operateList: [],

        tableColumns: [
          {
            title: 'ID',
            key: 'PlayerId',
            align: 'center',
          },
          {
            title: '',
            key: 'Mobile',
            align: 'center'
          },
          {
            title: '',
            key: 'LoginBanTime',
            align: 'center'
          }
        ],

        tableRows: []
      }
    },

    created() {
      this.fetchOperateList()
    },

    methods: {
      fetchOperateList() {
        this.operateList = [];
        let moneyNames = ["",""];
        for(var i = 0; i < moneyNames.length; i ++){
          this.operateList.push({"name": moneyNames[i], "type": i + 1})      
        }
      },

      onOperateChanged(value) {
        // this.formModel.operate = value;
      },

      searchSubmit() {
        // if (!services.checkGmtAuthor(2)) {
        //   this.$Message.error(",!");
        //   return
        // }

        // // id
        // var zoneId = this.formModel.selectedZoneId;
        // if (typeof zoneId === 'string') {
        //   this.$Message.error('');
        //   return
        // }

        // // id
        // var roleId = parseInt(this.formModel.roleId);
        // if (!roleId || roleId < 0) {
        //   this.$Message.error('id');
        //   return
        // }

        // 
        // var duration = 0;
        // if (this.formModel.type === 'ban') {
        //   duration = parseInt(this.formModel.banTime);
        //   if ((duration !== 0 && !duration) || duration <= 0) {
        //     this.$Message.error('');
        //     return
        //   }
        // }

        services.getHttpClient().post({
          url: '/dragon/banPlayerList',
          body: {
            username: services.getUser().username,
            platform: services.getUser().platform,
            page:1,
            pageSize:300
          }
        }, (error, response, body) => {
          if (error) {
            this.$Message.error(error.toString());
            return;
          }

          if (body.code != 0) {
            // this.$Message.error(","+body.code);
            return;
          }

          this.fillData(body.msg.value);
        });
      },

      fillData(rows) {
        this.tableRows.length = 0;
        rows.forEach((row) => {
          var tableRow = {};
          tableRow["PlayerId"] = row.PlayerId;
          tableRow["Mobile"] = row.Mobile;
          let banTime;
          if(parseInt(row.LoginBanTime) == -1){
            banTime = "";
          } else {
            banTime = services.getFormattedToDateString(row.LoginBanTime * 1000);
          }
          tableRow["LoginBanTime"] = banTime;

          this.tableRows.push(tableRow);
        });
      },

      /**
       * 
       * @param zoneId id
       * @param roleId id
       * @param duration 
       */
      handleSubmit() {
        let operateType = this.formModel.type == "ban" ? 1 : 2;
        let bantime = this.formModel.banTime;
        if(!bantime){
          bantime = 0;
        }
        let operateTime = parseInt(bantime);

        var type = this.formModel.typeSelect;
        var id = this.formModel.roleId;
        if (id.length === 0) {
          this.$Message.error('');
          return
        }

        let playerId = 0;
        let mobile = 0;
        if(type == "playerId"){
          playerId = id;
        } else if(type == "tel"){
          mobile = id;
        }

        services.getHttpClient().post({
          url: '/dragon/banPlayerEdit',
          body: {
            username: services.getUser().username,
            platform: services.getUser().platform,
            operateType:operateType,
            operateTime:operateTime,
             playerId:playerId,
             mobile:mobile
          }
        }, (error, response, body) => {
          if (error) {
            this.$Message.error(error.toString());
            return;
          }

          if (body.code != 0) {
            // this.$Message.error(","+body.code);
            return;
          }

          if(operateType == 1){
            this.$Message.success('');
          } else{
            this.$Message.success('');
          }
         

          this.searchSubmit();
        });
      }
    },

    components: {
      RegionSelect
    }
  }
</script>



