<template>
  <div>
    <RegionSelect ref="regionSelect"></RegionSelect>
    <br>
    <Form :model="formModel" :label-width="80">
      <!-- <FormItem label="">
        <RadioGroup v-model="formModel.type">
          <Radio label="playerId">ID</Radio>
          <Radio label="tel"></Radio>
        </RadioGroup>
      </FormItem>
      <FormItem label="id">
        <Input v-model="formModel.roleId" placeholder="ID"></Input>
      </FormItem> -->
      <FormItem>
        <Button type="primary" @click="handleSubmit"></Button>
      </FormItem>
      <Table border :columns="tableColumns" :data="tableRows"></Table>
    </Form>
  </div>
</template>

<script>
  import * as services from '@/services'
  import ZoneSelect from '@/components/ZoneSelect'
  import DatePicker from '@/components/DatePicker'
  import RegionSelect from '@/components/RegionSelect'
  import ChartTitle from '@/components/ChartTitle'

  export default {
    components: {
      ZoneSelect,
      DatePicker,
      RegionSelect,
      ChartTitle
    },

    data() {
      return {
        formModel: {
          type: 'playerId',
          roleId: ''
        },

        operateList: [],

        tableColumns: [
          {
            title: '',
            key: 'Mobile',
            align: 'center'
          },
          {
            title: '',
            key: 'Name',
            align: 'center'
          },
          {
            title: '',
            key: 'IdNum',
            align: 'center'
          },
          {
            title: '',
            key: 'CreateTime',
            align: 'center'
          },
          {
            title: 'id',
            key: 'PlayerId',
            align: 'center'
          },
          {
            title: '',
            key: 'LoginTime',
            align: 'center'
          },
          {
            title: '',
            key: 'LastLoginTime',
            align: 'center'
          },
          {
            title: '',
            key: 'TotalRechargeValue',
            align: 'center'
          },
          {
            title: 'USDT',
            key: 'Diamond',
            align: 'center'
          },
          {
            title: '',
            key: 'Money',
            align: 'center'
          },
          {
            title: '',
            key: 'KAD',
            align: 'center'
          },
          {
            title: '',
            key: 'InviteCode',
            align: 'center'
          },
          {
            title: '',
            key: 'KeyCount',
            align: 'center'
          },
          {
            title: '',
            key: 'DolphinSpeedCount',
            align: 'center'
          },
          {
            title: '',
            key: 'DolphinMoney',
            align: 'center'
          },
          {
            title: 'ID',
            key: 'SuperPlayerId',
            align: 'center'
          },
          {
              title: '',
              key: 'SuperMobile',
              align: 'center'
            },
          {
            title: '',
            key: 'DirectEffectNum',
            align: 'center'
          },
          {
            title: '',
            key: 'EffectNum',
            align: 'center'
          },
          {
            title: '',
            key: 'State',
            align: 'center'
          },
          {
            title: '',
            key: 'GameState',
            align: 'center'
          }
        ],

        tableRows: []
      }
    },

    created() {
      this.fetchOperateList()
    },

    methods: {
      onOperateChanged(value) {
        this.formModel.currencyType = value
      },
      fetchOperateList() {
        this.operateList = [];
        let moneyNames = ["","USDT","KAD","KEY","","",""];
        for(var i = 0; i < moneyNames.length; i ++){
          this.operateList.push({"name": moneyNames[i], "type": i + 1})      
        }
      },

      handleSubmit() {
        // var type = this.formModel.type;

        // var id = this.formModel.roleId;
        // if (id.length === 0) {
        //   this.$Message.error('');
        //   return
        // }

        // let playerId= 0;
        // let mobile;
        // if(type == "playerId"){
        //   playerId = id;
        // } else if(type == "tel"){
        //   mobile = id;
        // }

        services.getHttpClient().post({
            url: "/dragon/playerList",
            body: {
            username: services.getUser().username,
            platform: services.getUser().platform,
            page:1,
            pageSize:100
          }
        }, (error, response, body) => {
          if (error) {
            this.$Message.error(error.toString());
            return;
          }

          if (body.code != 0) {
            // this.$Message.error(body.msg);
            return;
          }

          this.fillData(body.msg.value);
        });
      },

      fillData(rows) {
        this.tableRows = [];
        rows.forEach((row) => {
          var tableRow = {};
          tableRow["Mobile"] = row.Mobile;
          tableRow["Name"] = row.Name;
          tableRow["IdNum"] = row.IdNum;
          tableRow["CreateTime"] = services.getFormattedToDateString(parseInt(row.CreateTime));
          tableRow["PlayerId"] = row.PlayerId;
          tableRow["LoginTime"] = services.getFormattedToDateString(parseInt(row.LoginTime)  * 1000);
          tableRow["LastLoginTime"] = row.LastLoginTime;
          tableRow["TotalRechargeValue"] = row.TotalRechargeValue;
          tableRow["Diamond"] = row.Diamond;
          tableRow["Money"] = row.Money;
          tableRow["KAD"] = row.KAD;
          tableRow["InviteCode"] = row.InviteCode;
          tableRow["KeyCount"] = row.KeyCount;
          tableRow["DolphinSpeedCount"] = row.DolphinSpeedCount;
          tableRow["DolphinMoney"] = row.DolphinMoney;
          tableRow["SuperPlayerId"] = row.SuperPlayerId;
          tableRow["SuperMobile"] = row.SuperMobile;
          tableRow["DirectEffectNum"] = row.DirectEffectNum;
          tableRow["EffectNum"] = row.EffectNum;
          tableRow["State"] = this.getStateMsg(row.State);
          tableRow["GameState"] = this.getGameStateMsg(row.GameState);

          this.tableRows.push(tableRow);
        });
      },

      getStateMsg(s) {
            //+,012
            let msg;
            switch (s) {
                case 0:
                    msg = ""
                    break;
                case 1:
                    msg = ""
                    break;
                case 2:
                    msg = ""
                    break;
            }
            return msg;
        },
      getGameStateMsg(s) {
            //A2:B,3
            let msg;
            switch (s) {
                case 0:
                    msg = ""
                    break;
                case 1:
                    msg = ""
                    break;
                case 2:
                    msg = ""
                    break;
                case 3:
                    msg = ""
                    break;
            }
            return msg;
        },
      onOperateChanged(value) {
        this.formModel.operate = value
      },
    }
  }
</script>

<style scoped>
</style>



